import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { selectProject } from '../../actions/user';
import { fetchProjectMembers } from '../../actions/api/members';

import CircularProgress from '../../components/lib/CircularProgress';

import CommitsReportsContainer from '../../components/report/share/Commits';

class Report extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
        };
    }

    componentWillMount() {
        this.props.selectProject(this.props.projectId);
        this.props.fetchProjectMembers(this.props.projectId)
            .then(() => {
                this.setState({ loading: false });
            });
    }

    render() {
        if (this.state.loading) {
            return <CircularProgress />;
        }
        return (
            <Fragment>
                <CommitsReportsContainer projectId={this.props.projectId} startDate={this.props.startDate} endDate={this.props.endDate} selectedRepoId={this.props.selectedRepoId} userId={this.props.userId} />
            </Fragment>
        );
    }
}

Report.propTypes = {
    projectId: PropTypes.string.isRequired,
    startDate: PropTypes.string.isRequired,
    endDate: PropTypes.string.isRequired,
    selectedRepoId: PropTypes.string.isRequired,
    userId: PropTypes.string.isRequired,
    selectProject: PropTypes.func.isRequired,
    fetchProjectMembers: PropTypes.func.isRequired,
};

function mapStateToProps(state, ownProps) {
    return {
        projectId: String(ownProps.match.params.id),
        startDate: String(ownProps.match.params.start_date),
        endDate: String(ownProps.match.params.end_date),
        selectedRepoId: String(ownProps.match.params.selected_repo_id),
        userId: String(ownProps.match.params.user_id),
    };
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectProject,
        fetchProjectMembers,
    }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Report);
