import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

import 'react-day-picker/lib/style.css';

import CommitViz from './Commits';
import CircularProgress from '../../../lib/CircularProgress';

import {
  fetchVizGithub,
} from '../../../../actions/api/viz';
import { allMembers } from '../../../../selectors/members';
import { notifyError } from '../../../../actions/notification';
import language from '../../../../constants/language';

/* eslint-disable max-len */
const styles = {
  titleContainer: {
    display: 'block',
    paddingLeft: 20,
    paddingTop: 5,
    borderBottom: '2px solid rgb(68, 68, 68)',
  },
  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  container: {
    display: 'grid',
    gridTemplateColumns: 'repeat(5, 1fr)',
    gridGap: '2px',
  },
  item2: {
    height: '500px',
    width: '500px',
    gridColumn: 'span 2 / auto',
  },
  item3: {
    height: '500px',
    width: '100%',
    gridColumn: 'span 3 / auto',
  },
  titleText: {
    color: '#2a3d41',
    fontSize: 18,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    alignSelf: 'center',
    fontSize: 14,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
    marginRight: '5px',
  },
  selectText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'normal',
    marginTop: '10px',
    marginBottom: '10px',
    marginLeft: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
  errorMsg: {
    marginLeft: '500px',
    marginTop: '200px',
    width: '200px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
    fontWeight: 'bold',
  },
  noDataMsg: {
    marginLeft: '100px',
    marginTop: '200px',
    marginBottom: '500px',
    width: '200px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
    fontWeight: 'bold',
  },
};

class UserContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      dataGit: [],
      repos: [],
    };
    this.changeDataGithub = this.changeDataGithub.bind(this);
  }

  componentDidMount() {
    this.changeDataGithub(
      this.props.startDate,
      this.props.endDate,
      this.props.userId,
      this.props.selectedRepoId,
    );
  }

  changeDataGithub(startDate, endDate, userId, selectedRepoId) {
    const { projectId } = this.props;
    const body = {
      start_date: startDate,
      end_date: endDate,
      repo_id: selectedRepoId,
    };
    this.props.fetchVizGithub(projectId, userId, body).then((response) => {
      const dataGit = response.ok ? response.body : [];
      this.setState({ loading: false, dataGit });
    });
  }


  render() {
    if (this.state.loading) return <CircularProgress />;
    const { classes, selectedLanguage } = this.props;
    const { dataGit } = this.state;
    const langDict = language[selectedLanguage].report;
    return (
      <div className={classes.headerContainer}>
        <Typography className={classes.title}>{langDict.commits}</Typography>
        <div className={classes.line} />
        <div className={classes.grid}>
          <div
            style={{ display: 'flex', alignSelf: 'center', marginLeft: '10px' }}
          >
            <Typography
              className={classes.text}>{langDict.user}:{' '}{this.props.userId}
            </Typography>
          </div>
          <div
            style={{ display: 'flex', alignSelf: 'center', marginLeft: '10px' }}
          >
            <Typography
              className={classes.text}>{langDict.from}:{' '}{this.props.startDate}
            </Typography>
          </div>
          <div
            style={{ display: 'flex', alignSelf: 'center', marginLeft: '10px' }}
          >
            <Typography
              className={classes.text}>{langDict.to}:{' '}{this.props.endDate}
            </Typography>
          </div>
          <div
            style={{ display: 'flex', alignSelf: 'center', marginLeft: '10px' }}
          >
            <Typography
              className={classes.text}>{langDict.repository}:{' '}{this.props.selectedRepoId}
            </Typography>
          </div>
        </div>
        <div className={classes.line} />
        <div className={classes.container}>
          {this.props.selectedRepo !== '' && dataGit.length > 0 && (
            <div className={classes.item3}>
              <CommitViz
                data={dataGit}
              />
            </div>
          )}
          {this.props.selectedRepo !== '' && dataGit.length === 0 && (
            <Typography className={classes.errorMsg}>
              {' '}
              {langDict.noCommitsFound}.
            </Typography>
          )}
          {this.state.repos.length === 0 && (
            <Typography className={classes.errorMsg}>
              {' '}
              {langDict.linkRepositoryFirst}.
            </Typography>
          )}
        </div>
      </div>
    );
  }
}

UserContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  users: PropTypes.array.isRequired,
  fetchVizGithub: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  selectedRepoId: PropTypes.string.isRequired,
  userId: PropTypes.string.isRequired,
  endDate: PropTypes.string.isRequired,
  startDate: PropTypes.string.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    user: state.project.trazabilityhoursUser,
    users: allMembers(ownProps, state),
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchVizGithub,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(UserContainer));
