import React, { Component }  from 'react';
import PropTypes from 'prop-types';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import language from '../../../../constants/language';
import CircularProgress from '../../../lib/CircularProgress';
import { connect } from 'react-redux';

class CommitViz extends Component{
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
    this.formatData.bind(this);
  }

  componentWillMount() {
    this.setState({ loading: false });
  }

  formatData () {

    const commits = [];
    const additions = [];
    const deletions = [];
    const xAxisCategories = []
    this.props.data.forEach((d) => {
      additions.push(
        d.a,
      );
      deletions.push(
        -d.d,
      );
      commits.push(
        d.c,
      );
      xAxisCategories.push(
        d.w,
      );
    });

    const series = [
      {
        name: 'Additions',
        data: additions,
        color: '#75ea7b',
      },
      {
        name: 'Deletions',
        data: deletions,
        color: '#f04a47',
      },
      {
        type: 'spline',
        name: 'Commits',
        data: commits,
        color: 'black',
      },
    ];

    return {'series': series, 'xAxis': xAxisCategories};
  }

  render () {
    if (this.state.loading) return <CircularProgress />;
    const {series , xAxis} = this.formatData();
    const { selectedLanguage } = this.props;
    (function (H) {
      // Pass error messages
      H.Axis.prototype.allowNegativeLog = true;

      // Override conversions
      H.Axis.prototype.log2lin = function (num) {
        var isNegative = num < 0,
          adjustedNum = Math.abs(num),
          result;
        if (adjustedNum < 10) {
          adjustedNum += (10 - adjustedNum) / 10;
        }
        result = Math.log(adjustedNum) / Math.LN10;
        return isNegative ? -result : result;
      };
      H.Axis.prototype.lin2log = function (num) {
        var isNegative = num < 0,
          absNum = Math.abs(num),
          result = Math.pow(10, absNum);
        if (result < 10) {
          result = (10 * (result - 1)) / (10 - 1);
        }
        return isNegative ? -result : result;
      };
    }(Highcharts));
    // style={{ margin:{ top: 20, right: 20, bottom: 60, left: 80, }}}
    return (
      <div>
        <HighchartsReact
          highcharts={Highcharts}
          constructorType={'chart'}
          options = {{
            chart: {
            type: 'area',
            },
            title: {
            text: language[selectedLanguage].report.commitsVizTitle,
            },
            subtitle: {
              text: language[selectedLanguage].report.commitsVizSubTitle,
            },
            xAxis: {
            categories: xAxis,
            },
            credits: {
            enabled: false,
            },
            yAxis :{
              type: 'logarithmic',
              title: {
                text: '',
              }
            },
            tooltip: {
              shared: true,
              formatter: function () {
                return this.points.reduce(function (s, point) {
                  const y = point.y < 0 ? -point.y : point.y;
                  return s + '<br/>' + point.series.name + ': ' + y;
                }, '<b>' + this.x + '</b>');
              },
            },
            series: series,
          }}
        />
      </div>
    );
  }
}

CommitViz.propTypes = {
  data: PropTypes.array.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

export default connect(
  mapStateToProps,
)(CommitViz);
